.button {
  touch-action: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  display: inline-block;
  border: 0.2em solid;
  position: relative;
  cursor: pointer;
  overflow: hidden;
  opacity: 0.6;
  color: #3bc2d3;
  z-index: 200; }

.button__text {
  display: block;
  padding: 1em 2em;
  text-transform: uppercase;
  font-weight: bold; }

.button__text:before {
  content: attr(title); }

.button__text--bis {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-transform: translateX(-1em);
  transform: translateX(-1em);
  opacity: 0; }

.button__mask {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: white;
  -webkit-transform: translateX(-100%) rotate(45deg);
  transform: translateX(-100%) rotate(45deg);
  -webkit-transition: all 0.3s;
  transition: all 0.3s; }

.button:hover {
  opacity: 1; }

.button:hover .button__text {
  -webkit-animation: fx-text 0.3s ease-out;
          animation: fx-text 0.3s ease-out; }

.button:hover .button__text--bis {
  -webkit-animation: fx-text-bis 0.3s ease-out;
          animation: fx-text-bis 0.3s ease-out; }

.button:hover .button__mask {
  -webkit-animation: fx-mask 0.3s ease-out;
          animation: fx-mask 0.3s ease-out; }

.button:active {
  opacity: 1;
  background: white;
  color: inherit; }

@-webkit-keyframes fadein {
